import * as React from 'react';
import { Box, Avatar, Typography, Stack, Divider, useTheme } from '@mui/material';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import EmailIcon from '@mui/icons-material/Email';
import CallIcon from '@mui/icons-material/Call';

export default function Header() {
  const theme = useTheme();

  return (
    <Box
      sx={{
        width: '96%',
        px: 3,
        py: 2,
        bgcolor: '#ffffff',
        borderRadius: 3,
        boxShadow: '0 2px 10px rgba(0,0,0,0.05)',
        display: 'flex',
        flexDirection: { xs: 'column', md: 'row' },
        alignItems: 'center',
        justifyContent: 'space-between',
        gap: 2,
      }}
    >
      {/* Left Section - Avatar & Name */}
      <Stack direction="row" alignItems="center" spacing={2}>
        <Avatar
          sx={{
            bgcolor: theme.palette.primary.main,
            color: '#fff',
            width: 48,
            height: 48,
            fontSize: '1.1rem',
            fontWeight: 'bold',
          }}
        >
          A
        </Avatar>
        <Box>
          <Typography fontWeight={600} fontSize="1rem">
            Atul Sharma
          </Typography>
          <Typography variant="caption" color="text.secondary">
            Software Developer
          </Typography>
        </Box>
      </Stack>

      {/* Divider on mobile */}
      <Divider flexItem sx={{ display: { xs: 'block', md: 'none' }, my: 1 }} />

      {/* Middle Section - Experience */}
      <Stack direction="row" spacing={1} alignItems="center">
        <AccountBoxIcon fontSize="small" color="action" />
        <Typography variant="body2" fontWeight={500}>
          13+ Yrs Experience
        </Typography>
      </Stack>

      {/* Right Section - Contact */}
      <Stack spacing={0.5}>
        <Stack direction="row" spacing={1} alignItems="center">
          <EmailIcon fontSize="small" color="action" />
          <Typography variant="body2" noWrap>
            atul762@gmail.com
          </Typography>
        </Stack>
        <Stack direction="row" spacing={1} alignItems="center">
          <CallIcon fontSize="small" color="action" />
          <Typography variant="body2">+91-893-960-9696</Typography>
        </Stack>
      </Stack>
    </Box>
  );
}
